// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchGoodsTotalCount() {
	return instance.get(`api/goods/totalCount`);
}

function fetchGoods(page) {
	return instance.get(`api/goods/all/${page}`);
}

function fetchGoodsDetail(id) {
	return instance.get(`api/goods/detail/${id}`);
}

function addGoods(data) {
	return instanceWithAuth.post('api/admin/goods/new', data);
}

function updateGoods(id, data) {
	return instanceWithAuth.put(`api/admin/goods/update/${id}`, data);
}

function deleteGoods(data) {
	return instanceWithAuth.delete('api/admin/goods/delete', { data });
}
function updateGoodsMainYn(id, mainYn) {
	return instanceWithAuth.put(`/api/admin/goods/setMainYn/${id}/${mainYn}`);
}
function updateGoodsStatus(status, data) {
	return instanceWithAuth.put(`/api/admin/goods/updateStatus/${status}`, data);
}

export {
	fetchGoodsTotalCount,
	fetchGoods,
	fetchGoodsDetail,
	addGoods,
	updateGoods,
	deleteGoods,
	updateGoodsMainYn,
	updateGoodsStatus,
};
